footer {
  background: linear-gradient(90deg, #4a90e2, #50c9c3);
  color: white;
  text-align: center;
  padding: 2rem 0;
}

.social-icons a {
  color: white;
  margin: 0 1rem;
  font-size: 1.5rem;
  transition: all 0.3s ease;
}

.social-icons a:hover {
  color: #f4c430;
  transform: scale(1.2);
}
